html {
  font-family: Cormorant, serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  -webkit-text-decoration: none;
  text-decoration: none;
  color: black;
}

ul {
  list-style: none;
}

main {
  padding: 0 3rem;
